.mainPage{
    position: absolute;
    width: 100%;
    min-height: 100%;
    height: fit-content;
    text-align: center;
    background-color: #332C39;
}

.header{
    position: relative;
    width: 100%;
    height: fit-content;
    font-size: 2.5em;
    box-shadow: 0px 1px 1px 0px black;
    text-align: center;
    top: 0%;
    padding-top:    0.2em;
    padding-bottom: 0.2em;
    background-color: #609EA2;
    color: #F0EEED;
    font-weight: bolder;
}
.mainBody {
    position: relative;
    display: inline-block;
    width: 80%;
    height: fit-content;
    margin-top: 2em;
    padding: 2%;
    text-align: left;
    overflow-x: hidden;
}

.portfolioContainer{
    position: relative;
    white-space: nowrap;
    height: fit-content;
}

.scrollButton{
    display: inline-block;
    position: absolute;
    top: 0%;
    height: 100%;
    background-color: none;
    width: 50px;
    font-size: 50px;
    background-color: rgba(0, 0, 0, 0);
    border-radius: 0px;
    border: none;
    z-index: 10;
    width: 10%;
    transition-duration: .3s;
}
.scrollButton:active{
    padding-top: 1px;
}

#rightButton{
    right: 0%;
}
#rightButton:hover{
    background-image: linear-gradient(to left, rgba(0, 0, 0, .3), rgba(255, 255, 255, 0));
}
#rightButton:active{
    background-image: linear-gradient(to left, rgba(255, 0, 0, .3), rgba(255, 255, 255, 0));
}

#leftButton{
    left: 0%;
}
#leftButton:hover{
    background-image: linear-gradient(to right, rgba(0, 0, 0, .3), rgba(255, 255, 255, 1));
}
#leftButton:active{
    background-image: linear-gradient(to right, rgba(255, 0, 0, .3), rgba(255, 255, 255, 1));
}

.toolTip{
    position: absolute;
    top:0%;
    right: 0%;
    transform: translate(102%, -80%);
    border: 1px solid grey;
    background-color: rgb(240, 240, 240);
    border-radius: 10px;
}


.pageHandler{
    position: relative;
    width: 84%;
    left: 8%;
    border-bottom: 1px solid white;
    text-align: left;
}
.pageTab{
    font-size: 1.5em;
    text-align: center;
    color: white;
    padding: 1em;
    display: inline-block;
}

.pageTab:hover{
    box-shadow: 0 -8px 10px -10px white inset;
}
.highlightedPageTab{
    color: red;
}
.pageTab > span {
    transform: translate(50%, 50%);
}





.infoPage{
    color: white;
    font-size: larger;
}



.techBox{
    display: inline-block;
    width: 150px;
    height: 150px;
    text-align: center;
    background-color: white;
    color:black;
    margin: 10px;
    padding: 2%;
    box-shadow: 0px 0px 2px 2px white;
    border-radius: 15px;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bolder;
    transition-property:all;
    transition-timing-function: ease;
    transition-duration: 1s;
}
.techBox:hover{
    box-shadow: 0px 0px 2px 2px hotpink;
}