.portfolioItem{
    width: 90%;
    height: 600px;
    box-shadow: 0px 0px 3px 1px #C92C6D;
    text-align: left;
    border-radius: 50px;
    display: inline-block;
    overflow-y: hidden;
    background-color: #F0EEED;
    white-space: pre-wrap;
    padding: 5%;
}
p{
    margin: 0%;
}

.link{
    position: relative;
    float: right;
    color: white;
    background-color: #C92C6D;
    padding: 10px;
    border-radius: 20px;
    z-index: 100;
}

@keyframes fancyLinkHover{
    0%{transform: rotate(0deg);}
    100%{transform: rotate(360deg);}
}

.link:hover{
    animation-name: fancyLinkHover;
    animation-duration: 1s;
    animation-iteration-count: 1;
}

a:visited, a:link{
    text-decoration: none;
    color: inherit;
}

